<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、登录名、手机号"
                      @keyup.native.enter="conditionQuery"
                      v-model="currencyTrans.keyword">
            </el-input>
            <span>虚拟币类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.currency">
                <el-option v-for="status in coinList" :key="status.baseSeq" :label="status.chineseName"
                           :value="status.baseSeq">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="conditionQuery" size="small" style="margin-left: 30px">查询
            </el-button>
            <el-button type="primary" @click="exportData" size="small" style="margin-left: 20px">导出数据
            </el-button>
        </div>
        <el-table
                ref="singleTable"
                :data="tableData"
                v-loading="listLoading"
                element-loading-text="加载数据中....">
            <el-table-column
                    type="index"
                    label="序号">
            </el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="currencyTrans.page"
                           :page-sizes="[15,30,45,60]" :page-size="currencyTrans.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {currencywallet, exportData} from '@/api/capital';
    import calc from "@/utils/calculate-big-num";

    const allCoin = {baseSeq: 0, chineseName: '全部类型', englishName: ""};
    export default {
        data() {
            return {
                total: 0,
                allCoin,
                listLoading: false,
                tableData: [],
                coinList: [],
                currencyTrans: {
                    //标识为虚拟币查询
                    type: 1,
                    keyword: '',
                    currency: 0,
                    page: 1,
                    pageSize: 15
                },
                tableHeader: [{
                    label: "会员UID",
                    prop: "userid",
                }, {
                    label: "姓名",
                    prop: "realName",
                }, {
                    label: "手机号码",
                    prop: "phoneNum",
                }, {
                    label: "邮箱",
                    prop: "email",
                }, {
                    label: "虚拟币类型",
                    prop: "coinName",
                    align: 'center'
                }, {
                    label: "可用余额",
                    prop: "availMoney",
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                },{
                    label: "otc余额",
                    prop: "otcAmount",
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "挂单金额",
                    prop: "ordersAmount",
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "冻结金额",
                    prop: "freezeMcnt",
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "总金额",
                    prop: "cashAmount",
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                },{
                    label: "最后修改时间",
                    prop: "updateTime",
                }]
            }
        },
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        },
        methods: {
            //获取当前页数
            userPage(val) {
                this.currencyTrans.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.pageSize = val;
                this.getList();
            },
            conditionQuery() {
                this.getList();
            },
            //查询所有列表以及条件查询
            getList() {
                this.listLoading = true
                currencywallet(this.currencyTrans).then(response => {
                    this.total = response.data.total;
                    this.tableData = response.data.list;
                    this.coinList = response.data.coinList;
                    this.coinList.unshift(this.allCoin);
                    if (this.total <= 0) {
                        this.msg = "未查询到此筛选数据";
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            },
            //导出数据
            exportData() {
                this.listLoading = true
                exportData(this.currencyTrans).then(response => {
                    this.listLoading = false;
                })
            }
        }, created() {
            this.getList();
        }
    }
</script>



